	module.exports = {"data":{"wordpressPost":{"title":"Pre-teens whose dads embrace parenthood may be less prone to behavioural issues","content":"<p><em>How new fathers see themselves in child’s early years seems to be most influential</em></p>\n<p><a href=\"http://bmjopen.bmj.com/lookup/doi/10.1136/bmjopen-2016-012034\">Kids whose dads adjust well to parenthood and feel confident about their new role may be less likely to have behavioural problems in the run-up to their teens, indicates research</a> published in the online journal <strong><em><a href=\"http://bmjopen.bmj.com/\">BMJ Open</a>.</em></strong></p>\n<p>It’s how fathers see themselves as parents in the child’s early years, rather than the amount of direct childcare they give, that seems to be important, the findings suggest.</p>\n<p>The nature of parenting in a child’s early years is thought to influence their short and long term wellbeing and mental health, which are in turn linked to development and educational attainment.</p>\n<p>But it’s not entirely clear what impact the father’s role might have, as much of the research to date has tended to characterise paternal involvement in a child’s upbringing as one dimensional.</p>\n<p>The researchers therefore drew on data from the Avon Longitudinal Study of Parents and Children (ALSPAC) study, which has been tracking the health of nearly 15,000 children since birth, to assess several aspects of paternal involvement.</p>\n<p>The parents of 10,440 children who were living with both their mum and dad at the age of 8 months were asked to complete a comprehensive questionnaire about their and their child’s mental health; their attitudes to parenting; time spent on childcare; their child’s behaviour and development; as well as details of household income/education.</p>\n<p>When the children were aged 9 and 11, their behaviour was assessed using the strength and difficulties questionnaire (SDQ). This covers emotional symptoms, behaviour (conduct) problems, hyperactivity, peer relationship issues, and helpfulness (pro-social behaviour).</p>\n<p>Fathers’ parental involvement was measured by asking them to rate their level of agreement with 58 statements, reflecting the amount of direct childcare they engaged in, including household chores; their attitudes to parenting; the relationship with their child; and how they felt about the birth 8 weeks and 8 months afterwards.</p>\n<p>The final analysis was based on almost 7000 9 year olds and nearly 6500 of the same children at the age of 11.</p>\n<p>Three key factors emerged in relation to the children’s SDQ scores: (1)  fathers’ emotional response to the baby and their parenting role; (2) how much time the dads spent on direct childcare; and (3) how well they adjusted to their new role, including how confident they felt in their abilities as a parent and partner.</p>\n<p>But emotional response and confidence in their new role were most strongly associated with lower odds of behavioural problems when their children reached 9 and 11 years of age.</p>\n<p>A high paternal factor 1 score was associated with 21% and 19% lower odds of a higher SDQ score at the ages of 9 and 11, respectively. Similarly, a high paternal factor 3 score was associated with 28% lower odds of a higher SDQ score at both time points.</p>\n<p>After taking account of potentially influential factors, such as age at fatherhood, educational attainment and household income, hours worked, and sex of the child, every unit increase in factor 1 scores was associated with 15% lower odds of behavioural problems at the age of 9 and 12% lower odds at the age of 11, compared with kids of the same age, sex, and family background.</p>\n<p>Similarly, every unit increase in factor 3 scores was associated with 12% lower odds at the age of 9, and 10% lower odds at the age of 11.</p>\n<p>This is an observational study so no firm conclusions can be drawn about cause and effect, and as the researchers note, the study dates back 25 years, since when parenting styles may have changed, so the findings may therefore not be widely generalisable.</p>\n<p>But they write: “The findings of this research study suggest that it is psychological and emotional aspects of paternal involvement in a child’s infancy that are most powerful in influencing later child behaviour, and not the amount of time that fathers are engaged in childcare or domestic tasks in the household.”<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"ab0c8ced-6415-5cd4-adba-6a52cc3ebba6"}}

